export const connectToDeviceRequest = createAction('CONNECT_TO_DEVICE_REQUEST');
export const connectToDeviceSuccess = createAction('CONNECT_TO_DEVICE_SUCCESS');
export const connectToDeviceFailure = createAction('CONNECT_TO_DEVICE_FAILURE');

//    
export const connectToDevice = (newFilters) => {
    return (dispatch, getState) => {
        const {
            // bluetooth: { devices: devicesFromStore, device: deviceFromStore },
            bluetooth: { device: deviceFromStore },
        } = getState();

        // alert('action connectToDevice');

        dispatch(connectToDeviceRequest());

        const filters = newFilters;
        // const filters = newFilters || [{ namePrefix: 'Pipe' }];
        // const filters = newFilters || [{ namePrefix: 'Blue' }];
        // const filters = newFilters;
        navigator.bluetooth
            .requestDevice({
                acceptAllDevices: filters ? undefined : true,
                filters,
                optionalServices: [
                    dataTransferService.uuid,
                    recipeInfoService.uuid,
                    commandService.uuid,
                    'battery_service',
                    0x2a29,
                    0x180a,
                ],
            })
            .then((device) => {
                // if (
                //     _.find(
                //         devicesFromStore,
                //         (deviceFromStore) =>
                //             deviceFromStore.name === device.name
                //     )
                // )
                if (deviceFromStore && deviceFromStore.name === device.name) {
                    throw new Error('device has already been added');
                } else {
                    console.log(
                        '%cconnected device:',
                        'color:green; font-size:2em; border-top:10px solid red'
                    );
                    console.log(device);

                    //      
                    device.ongattserverdisconnected = (event) =>
                        dispatch(onDisconnected(event));

                    // device.addEventListener('gattserverdisconnected', (event) =>
                    //     dispatch(onDisconnected(event))
                    // );

                    dispatch(connectToDeviceSuccess(device));

                    //    
                    dispatch(saveDevice(device));

                    //    GATT
                    dispatch(connectToGATTServer(device));
                }
            })
            .catch((error = { message: 'connectToDeviceFailure' }) => {
                console.log(error.message);
                dispatch(connectToDeviceFailure());
            });
    };
};

export const deviceDisconnected = createAction('DEVICE_DISCONNECTED');
export const failedToReconnect = createAction('FAILED_TO_RECONNECT');
