//    
export const onDisconnected = (event) => {
    return (dispatch) => {
        dispatch(deviceDisconnected());

        const device = event.target;

        console.log(`Device ${device.name} is disconnected.`);
        // console.log(event);

        dispatch(connectToGATTServer(device));
    };
};

