//   GATT   
export const connectToGATTServer = (device) => {
    return (dispatch, getState) => {
        exponentialBackoff(
            3 /* max retries */,
            2 /* seconds delay */,
            function toTry() {
                dispatch(connectGattRequest({ device }));
                time('Connecting to Bluetooth Device... ');
                return device.gatt.connect();
            },
            function success(server) {
                console.log(
                    '%cGATT server:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.log(server);

                dispatch(connectGattSuccess({ server, device }));

                //    .   

                //    Data Transfer Service
                server
                    .getPrimaryService(dataTransferService.uuid)
                    .then((service) => {
                        dispatch(connectDataTransferServiceSuccess(service));
                        return service.getCharacteristic(
                            dataTransferService.characteristics.controlTransmit
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleControlTransmit(event, dispatch, getState)
                        );

                        const {
                            bluetooth: { dataTransferService: service },
                        } = getState();

                        return service.getCharacteristic(
                            dataTransferService.characteristics.status.uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleStatusTransferData(
                                    event,
                                    dispatch,
                                    getState
                                )
                        );

                        const {
                            bluetooth: { dataTransferService: service },
                        } = getState();

                        return service.getCharacteristic(
                            dataTransferService.characteristics.dataTransmit
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleTransmitData(event, dispatch, getState)
                        );
                    })
                    .catch((error) => {
                        console.log('DataTransferService', error);
                    });

                //    Recipe Info Service
                server
                    .getPrimaryService(recipeInfoService.uuid)
                    .then((service) => {
                        dispatch(connectRecipeInfoServiceSuccess(service));

                        return service.getCharacteristic(
                            recipeInfoService.characteristics.recipeListLowPart
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleRecipeList(event, dispatch, getState)
                        );

                        return characteristic.readValue();
                    })
                    .then((value) => {
                        console.log('RV - recipeListLowPart: ', value.buffer);

                        dispatch(
                            fetchRecipesIDOnDevice({
                                recipesIDList: {
                                    [value.getUint32(0, true)]: value.getUint16(
                                        4,
                                        true
                                    ),
                                    [value.getUint32(6, true)]: value.getUint16(
                                        10,
                                        true
                                    ),
                                    [value.getUint32(
                                        12,
                                        true
                                    )]: value.getUint16(16, true),
                                },
                                from: 'lowPart',
                            })
                        );

                        const {
                            bluetooth: { recipeInfoService: service },
                        } = getState();

                        return service.getCharacteristic(
                            recipeInfoService.characteristics.recipeListMidPart
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleRecipeList(event, dispatch, getState)
                        );

                        return characteristic.readValue();
                    })
                    .then((value) => {
                        console.log('RV - recipeListMidPart: ', value.buffer);

                        dispatch(
                            fetchRecipesIDOnDevice({
                                recipesIDList: {
                                    [value.getUint32(0, true)]: value.getUint16(
                                        4,
                                        true
                                    ),
                                    [value.getUint32(6, true)]: value.getUint16(
                                        10,
                                        true
                                    ),
                                    [value.getUint32(
                                        12,
                                        true
                                    )]: value.getUint16(16, true),
                                },
                                from: 'midPart',
                            })
                        );

                        const {
                            bluetooth: { recipeInfoService: service },
                        } = getState();

                        return service.getCharacteristic(
                            recipeInfoService.characteristics.recipeListHighPart
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleRecipeList(event, dispatch, getState)
                        );

                        return characteristic.readValue();
                    })
                    .then((value) => {
                        console.log('RV - recipeListHighPart: ', value.buffer);

                        dispatch(
                            fetchRecipesIDOnDevice({
                                recipesIDList: {
                                    [value.getUint32(0, true)]: value.getUint16(
                                        4,
                                        true
                                    ),
                                    [value.getUint32(6, true)]: value.getUint16(
                                        10,
                                        true
                                    ),
                                    [value.getUint32(
                                        12,
                                        true
                                    )]: value.getUint16(16, true),
                                },
                                from: 'highPart',
                            })
                        );

                        const {
                            bluetooth: { recipeInfoService: service },
                        } = getState();

                        return service.getCharacteristic(
                            recipeInfoService.characteristics.recipeListSize
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) =>
                                handleSizeOfRecipeList(
                                    event,
                                    dispatch,
                                    getState
                                )
                        );

                        return characteristic.readValue();
                    })
                    .then((value) => {
                        console.log(
                            'RV - handleSizeOfRecipeList: ',
                            value.buffer
                        );
                    })
                    .catch((error) => {
                        console.log('RecipeInfoService', error);
                    });

                //    
                server
                    .getPrimaryService(commandService.uuid)
                    .then((service) => {
                        dispatch(connectCommandServiceSuccess(service));

                        return service.getCharacteristic(
                            commandService.characteristics.command.uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            (event) => handleCommand(event, dispatch, getState)
                        );
                    })
                    .catch((error) => {
                        console.log('CommandService', error);
                    });

                try {
                    //   
                    dispatch(batteryService());

                    //    
                    // dispatch(getLogs());

                    //    
                    // dispatch(recipeInfo());
                } catch (e) {
                    console.log('catch from success GATT', e.message);
                }
            },
            function fail() {
                dispatch(connectGattFailure({ device }));
                time('Failed to connect.');

                Modal.error({
                    title: 'Failed to reconnect',
                    content: 'Please try again later',
                });
            }
        );

        function exponentialBackoff(max, delay, toTry, success, fail) {
            toTry()
                .then((server) => success(server))
                .catch((error) => {
                    console.log('catch', error);
                    if (max === 0) {
                        return fail();
                    }
                    time(
                        'Retrying in ' + delay + 's... (' + max + ' tries left)'
                    );
                    setTimeout(function () {
                        exponentialBackoff(
                            --max,
                            delay * 2,
                            toTry,
                            success,
                            fail
                        );
                    }, delay * 1000);
                });
        }

        function time(text) {
            console.log('[' + new Date().toJSON().substr(11, 8) + '] ' + text);
        }
    };
};

export const disconnectGattServer = (device) => {
    return () => {
        // console.log('disconnectGattServer', device);
        device.device.gatt.disconnect();
    };
};

