export const fetchBatteryServiceRequest = createAction(
    'FETCH_BATTERY_SERVICE_REQUEST'
);
export const fetchBatteryServiceSuccess = createAction(
    'FETCH_BATTERY_SERVICE_SUCCESS'
);
export const fetchBatteryServiceFailure = createAction(
    'FETCH_BATTERY_SERVICE_FAILURE'
);

export const fetchBatteryCharacteristicSuccess = createAction(
    'FETCH_BATTERY_CHARACTERISTIC_SUCCESS'
);

export const batteryService = () => {
    return (dispatch, getState) => {
        dispatch(fetchBatteryServiceRequest());

        const {
            bluetooth: { server },
        } = getState();
        // console.log('1');

        // HelperOfDevice.readMyProp();

        let service = null;
        server
            .getPrimaryService('battery_service')
            .then((ser) => {
                service = ser;
                dispatch(fetchBatteryServiceSuccess());
                // console.log('2');
                return service.getCharacteristic('battery_level');
            })
            .catch(() => {
                // console.log('3');
                dispatch(fetchBatteryServiceFailure());
            })
            .then((characteristic) => {
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                // console.log('4');
                // if (characteristic) {
                //     console.log('4.1');
                //     characteristic.startNotifications();
                //     characteristic.addEventListener(
                //         'characteristicvaluechanged',
                //         handleBatteryLevelChange
                //     );
                //     return characteristic.readValue();
                // }
                // throw new Error('characteristic baterry level not found');

                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    handleBatteryLevelChange
                );

                return characteristic.readValue();
            })
            .then((value) => {
                // console.log('Battery service', value.buffer);
                // console.log('5');
                dispatch(
                    fetchBatteryServiceSuccess({ level: value.getUint8(0) })
                );
            })
            .catch(() => {});

        function handleBatteryLevelChange(event) {
            dispatch(
                fetchBatteryServiceSuccess({
                    level: event.target.value.getUint8(0),
                })
            );
        }
    };
};

export const fetchDeviceInfoRequest = createAction('FETCH_DEVICE_INFO_REQUEST');
export const fetchDeviceInfoSuccess = createAction('FETCH_DEVICE_INFO_SUCCESS');
export const fetchDeviceInfoFailure = createAction('FETCH_DEVICE_INFO_FAILURE');

export const fetchDeviceInfoCharacteristicSuccess = createAction(
    'FETCH_DEVICE_INFO_CHARACTERISTIC_SUCCESS'
);

export const deviceInfoService = () => {
    return (dispatch, getState) => {
        dispatch(fetchDeviceInfoRequest());

        const {
            bluetooth: { server },
        } = getState();

        server
            .getPrimaryService(0x180a)
            .then((service) => {
                dispatch(fetchDeviceInfoSuccess());
                return service.getCharacteristic(0x2a29);
            })
            .catch(() => {
                dispatch(fetchDeviceInfoFailure());
            })
            .then((characteristic) => {
                if (characteristic) {
                    return characteristic.readValue();
                }
                throw new Error('characteristic manufacturerName not found');
            })
            .then((value) => {
                dispatch(
                    fetchDeviceInfoCharacteristicSuccess({
                        manufacturerName: value.getUint8(0),
                    })
                );
            })
            .catch(() => {});
    };
};

