let keyI = 0;
let characteristicWrite = null;

export const writeRecipeToDevice = (recipeID) => {
    return (dispatch, getState) => {
        const {
            bluetooth: { server },
            auth: {
                cookie: { access_token },
            },
        } = getState();

        dispatch(getBinaryRecipeRequest());
        dispatch(showAppPreloader(true));

        axios
            .get('recipe_binary/' + recipeID + '/', {
                headers: {
                    Authorization: `Bearer ${access_token}`,
                },
            })
            .then((response) => {
                dispatch(getBinaryRecipeSuccess(response.data));
                dispatch(showAppPreloader(false));

                // Send control msg to device before transfer data
                server
                    .getPrimaryService(dataTransferService.uuid)
                    .then((service) => {
                        return service.getCharacteristic(
                            dataTransferService.characteristics.controlReceive
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        const {
                            data: { recipeBinary },
                        } = getState();

                        let buffer = Utils.prepareControlReceiveBuffer(
                            'writeRecipeRequest',
                            {
                                length: recipeBinary.buffer.byteLength,
                            }
                        );

                        console.log(
                            '%ccontrol receive:',
                            'color:green; font-size:2em; border-top:10px solid red'
                        );
                        console.log(new Int8Array(buffer));
                        setTimeout(() => {
                            characteristic.writeValue(new Int8Array(buffer));
                        }, 1000);
                    })
                    .catch((error) => {
                        console.log(
                            'Error writeValue to Control Receive: ',
                            error
                        );
                    });
            })
            .catch((error) => {
                console.log('ERROR - getBinaryRecipe', error);
                dispatch(getBinaryRecipeFailure());
                dispatch(showAppPreloader(false));
            });
    };
};

