export const updateRecipeOnDevice = (recipeID) => {
    return (dispatch, getState) => {
        const {
            bluetooth: { server },
            // data: { recipeBinary },
        } = getState();

        // Send control msg to device before transfer data
        server
            .getPrimaryService(commandService.uuid)
            .then((service) => {
                return service.getCharacteristic(
                    // commandService.characteristics.command.uuid
                    'dad54501-2ee3-6213-624a-aeca328f132a'
                );
            })
            .then((characteristic) => {
                // let buffer = Utils.prepareCommandBuffer('removeRecipe', {
                //     id: recipeID,
                // });

                let buffer = new ArrayBuffer(20);
                let msgKeyWord8 = 0x12;

                new DataView(buffer).setInt8(0, msgKeyWord8, true);
                new DataView(buffer).setInt32(1, Number(recipeID), true);
                // return buffer;

                console.log(
                    '%ccommand:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.log(new Int8Array(buffer));

                setTimeout(() => {
                    characteristic.writeValue(new Int8Array(buffer));
                }, 1000);
            })
            .catch((error) => {
                console.log('ERROR - WV command: ', error);
            });
    };
};

