function handleControlTransmit(event, dispatch, getState) {
    const {
        bluetooth: { server },
        data: { recipeBinary },
    } = getState();

    let status = event.target.value.getUint8(0),
        operationCode = event.target.value.getUint8(1),
        dataLength = event.target.value.getUint32(2, true),
        dataType = event.target.value.getUint16(6, true);
    console.log(
        'handleControlTransmit',
        status,
        '----',
        operationCode,
        '----',
        dataLength,
        '----',
        dataType,
        '----',
        event.target.value.buffer
    );
    if (0x0a === status && dataType === 0x6 && operationCode === 0xff) {
        //  
        console.log('prepare to sending recipe...');

        server
            .getPrimaryService(dataTransferService.uuid)
            .then((service) => {
                return service.getCharacteristic(
                    dataTransferService.characteristics.dataReceive.uuid
                );
            })
            .then((characteristic) => {
                characteristicWrite = characteristic;

                dispatch(
                    startTransferOfRecipe({
                        characteristicForWrite: characteristic,
                    })
                );

                setTimeout(() => sendingData(recipeBinary, dispatch), 2000);
            })
            .catch((error) => {
                console.log('ERROR - prepare to sending recipe: ' + error);
            });
    } else if (0x0a === status && dataType === 0x2 && operationCode === 0xff) {
        //  
        console.log('prepare to sending img...');

        setTimeout(
            () =>
                server
                    .getPrimaryService(dataTransferService.uuid)
                    .then((service) => {
                        return service.getCharacteristic(
                            dataTransferService.characteristics.dataReceive.uuid
                        );
                    })
                    .then((characteristic) => {
                        characteristicWrite = characteristic;

                        dispatch(startTransferOfImg());

                        sendingDataImg(recipeBinary, dispatch);
                    })
                    .catch((error) => {
                        console.log('ERROR - prepare to sending img: ' + error);
                    }),
            2000
        );
    } else if (0x0f === status && dataType === 0x6) {
        //  
        console.log('Recipe did saved');

        dispatch(endTransferOfRecipe());

        setTimeout(() => {
            server
                .getPrimaryService(dataTransferService.uuid)
                .then((service) => {
                    return service.getCharacteristic(
                        dataTransferService.characteristics.controlReceive.uuid
                    );
                })
                .then((characteristic) => {
                    let buffer = Utils.prepareControlReceiveBuffer(
                        'writeImgRequest',
                        {
                            length: recipeBinary.bufferImg.byteLength,
                        }
                    );

                    console.log(
                        '%ccontrol receive:',
                        'color:green; font-size:2em; border-top:10px solid red'
                    );
                    console.log(
                        new Int8Array(buffer),
                        recipeBinary.bufferImg.byteLength
                    );
                    characteristic.writeValue(new Int8Array(buffer));
                })
                .catch((error) => {
                    alert(error);
                });
        }, 2000);
    } else if (0x0f === status && dataType === 0x2) {
        //  
        console.log('Img did saved');

        dispatch(endTransferOfImg());

        dispatch(showAppPreloader(false));
    } else if (0x0d === status) {
        //  
        console.warn("Don't have space");
    } else if (0x0a === status && dataType === 0x7 && dataLength) {
        //     
        let logs = new ArrayBuffer(dataLength);

        dispatch(startReceiveOfLogs({ logs }));

        server
            .getPrimaryService(dataTransferService.uuid)
            .then((service) => {
                return service.getCharacteristic(
                    dataTransferService.characteristics.controlReceive.uuid
                );
            })
            .then((characteristic) => {
                let buffer = Utils.prepareControlReceiveBuffer(
                    'fetchLogsReady'
                );

                console.log(
                    '%ccontrol receive:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.log(new Int8Array(buffer));

                characteristic.writeValue(new Int8Array(buffer));
            })
            .catch((error) => {
                console.log(
                    'ERROR - WV Control Receive I am ready to get logs: ' +
                        error
                );
            });
    }

    console.log(
        '%ccontrol transmit:',
        'color:green; font-size:2em; border-top:10px solid red'
    );

    switch (status) {
        case 0x0a:
            console.log(
                '%cI am ready',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            keyI = 0;
            break;
        case 0x0b:
            console.log(
                '%cPaused',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
        case 0x0c:
            console.log(
                '%cAborted',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
        case 0x0d:
            console.log(
                '%cDont`t have space',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
        case 0x0e:
            console.log(
                '%cBusy',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
        case 0x0f:
            console.log(
                '%cSuccess',
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
        default:
            console.log(
                '%cUndefined status: ' + status,
                'color:black; font-size:1em; border: 1px dashed gray;'
            );
            break;
    }
}

function handleTransmitData(event, dispatch, getState) {
    const {
        data: {
            logsTransfering: { logs, keyI },
        },
        bluetooth: { device },
    } = getState();

    let value = event.target.value;

    console.log(
        'handleTransmitData',
        value.buffer,
        value.getUint8(0),
        'logs byteLength: ' + logs.byteLength,
        'logs buffer: ' + logs
    );

    let i = 0;
    while (i < 20) {
        if (keyI >= logs.byteLength) {
            break;
        }
        // console.log('Uint8', value.getUint8(i));
        new DataView(logs).setInt8(keyI, value.getUint8(i));
        keyI++;
        i++;
    }
    // console.log(logs);

    if (keyI === logs.byteLength) {
        let base64String = btoa(String.fromCharCode(...new Uint8Array(logs)));
        console.log('base64', base64String);
        dispatch(fetchLogsSuccess(base64String));
        dispatch(addHistory(base64String, device.name));
    }
}

function handleStatusTransferData(event, dispatch, getState) {
    const {
        data: {
            recipeBinary,
            recipeAndImgAreTransfering: {
                recipeIsTransfering,
                imgIsTransfering,
            },
        },
    } = getState();

    //      ,   
    if (recipeIsTransfering) {
        let value = event.target.value;
        console.log(
            'handleStatusTransferData',
            value.buffer,
            value.getUint32(0),
            value.getUint8(4)
        );
        sendingData(recipeBinary, dispatch);
    } else if (imgIsTransfering) {
        let value = event.target.value;
        console.log(
            'handleStatusTransferDataImg',
            value.buffer,
            value.getUint32(0)
        );
        sendingDataImg(recipeBinary, dispatch);
    } else {
        let value = event.target.value;
        console.log('handleStatusTransferData', value, value.getUint8(0));
    }
}

