import { handleActions } from 'redux-actions';
import * as actions from '../actions';
import * as bluetoothActions from '../actions/bluetooth';
import { base64ToArrayBuffer } from '../utils';

export default handleActions(
    {
        [actions.fetchRecipesSuccess](state, { payload }) {
            return {
                ...state,
                recipe: payload,
            };
        },
        [actions.fetchUserSuccess](state, { payload }) {
            return {
                ...state,
                user: payload,
            };
        },
        [actions.fetchUserDevicesSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.getBinaryRecipeSuccess](state, { payload }) {
            let buffer = base64ToArrayBuffer(payload.binary_recipe);
            let bufferImg = base64ToArrayBuffer(payload.binary_image_r400x400);
            return {
                ...state,
                recipeBinary: { ...payload, buffer, bufferImg },
            };
        },
        [bluetoothActions.recipeByteTransfered](state, { payload }) {
            return {
                ...state,
                byteTransfered: state.byteTransfered + payload,
            };
        },
        [bluetoothActions.imgByteTransfered](state, { payload }) {
            return {
                ...state,
                imgByteTransfered: state.imgByteTransfered + payload,
            };
        },
        [bluetoothActions.speedOfDataTransfer](state, { payload }) {
            console.log('Speed', payload);
            return {
                ...state,
                speedOfDataTransfer: isFinite(payload) ? payload : 0,
            };
        },
        [actions.saveDeviceSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.removeDeviceSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [bluetoothActions.fetchLogsSuccess](state, { payload }) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'success', data: payload }
                ),
            };
        },
        [bluetoothActions.fetchLogsFailure](state) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'failure' }
                ),
            };
        },
        [bluetoothActions.fetchLogsRequest](state) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'request' }
                ),
            };
        },
        [bluetoothActions.startTransferOfRecipe](
            state,
            { payload: { characteristicForWrite } }
        ) {
            return {
                ...state,
                recipeAndImgAreTransfering: {
                    recipeIsTransfering: true,
                    imgIsTransfering: false,
                    characteristicForWrite,
                    keyI: 0,
                },
            };
        },
        [bluetoothActions.endTransferOfRecipe](state) {
            return {
                ...state,
                recipeAndImgAreTransfering: {
                    ...state.recipeAndImgAreTransfering,
                    recipeIsTransfering: false,
                    imgIsTransfering: false,
                    keyI: 0,
                },
            };
        },
        [bluetoothActions.startTransferOfImg](state) {
            return {
                ...state,
                recipeAndImgAreTransfering: {
                    ...state.recipeAndImgAreTransfering,
                    recipeIsTransfering: false,
                    imgIsTransfering: true,
                    keyI: 0,
                },
            };
        },
        [bluetoothActions.endTransferOfImg](state) {
            return {
                ...state,
                recipeAndImgAreTransfering: {
                    recipeIsTransfering: false,
                    imgIsTransfering: false,
                    keyI: 0,
                    characteristicForWrite: null,
                },
            };
        },
        [bluetoothActions.fetchRecipesIDOnDevice](
            state,
            { payload: { recipesIDList, from } }
        ) {
            let copyObjFromStore = Object.assign({}, state.recipesOnDevice);

            switch (from) {
                case 'lowPart':
                    return {
                        ...state,
                        recipesOnDevice: recipesIDList,
                    };
                case 'midPart':
                    Object.keys(recipesIDList).forEach((key) => {
                        copyObjFromStore[key] = recipesIDList[key];
                    });
                    return {
                        ...state,
                        recipesOnDevice: copyObjFromStore,
                    };
                case 'highPart':
                    Object.keys(recipesIDList).forEach((key) => {
                        copyObjFromStore[key] = recipesIDList[key];
                    });
                    return {
                        ...state,
                        recipesOnDevice: copyObjFromStore,
                    };
                default:
                    break;
            }
            return {
                ...state,
            };
        },
        [bluetoothActions.startReceiveOfLogs](state, { payload }) {
            return {
                ...state,
                logsTransfering: {
                    ...state.logsTransfering,
                    logs: payload.logs,
                },
            };
        },
        [bluetoothActions.receivedLogs](state, { payload }) {
            return {
                ...state,
                logsTransfering: {
                    ...state.logsTransfering,
                    logs: payload.logs,
                },
            };
        },
        [bluetoothActions.endReceiveOfLogs](state, { payload }) {
            return {
                ...state,
                logsTransfering: {
                    ...state.logsTransfering,
                    logs: payload.logs,
                },
            };
        },
    },
    {
        recipe: [],
        user: null,
        userDevices: null,
        recipeBinary: null,
        byteTransfered: 0,
        imgByteTransfered: 0,
        speedOfDataTransfer: 0,
        logs: { status: 'success', data: null },
        recipeAndImgAreTransfering: {
            recipeIsTransfering: false,
            imgIsTransfering: false,
            keyI: 0,
            characteristicForWrite: null,
        },
        logsTransfering: {
            logs: null,
            keyI: 0,
        },
        recipesOnDevice: {},
    }
);
