import React, { Component } from 'react';
import { Card, Row, Button, Divider } from 'antd';
import { connect } from 'react-redux';
import * as actions from '../../../actions';
import * as bluetoothActions from '../../../actions/bluetooth';

import { CheckCircleTwoTone } from '@ant-design/icons';

const mapStateToProps = (state) => {
    const {
        auth: { token },
        data: { recipesOnDevice },
    } = state;
    return {
        token,
        recipesOnDevice,
    };
};

const actionCreators = {
    writeRecipeToDevice: bluetoothActions.writeRecipeToDevice,
    getBinaryRecipe: actions.getBinaryRecipe,
    updateRecipeOnDevice: bluetoothActions.updateRecipeOnDevice,
};

class Recipes extends Component {
    render() {
        const {
            onClose,
            // getBinaryRecipe,
            writeRecipeToDevice,
            recipesOnDevice,
            updateRecipeOnDevice,
        } = this.props;
        let recipes = this.props.recipes.map((recipe) => {
            return (
                // <Col span={6} key={recipe.id}>
                <Card
                    key={recipe.id}
                    // title={recipe.name + ' - id' + recipe.id + ''}
                    title={
                        <div>
                            {/* <p> */}
                            {recipesOnDevice[recipe.id] ? (
                                <h3>
                                    <CheckCircleTwoTone twoToneColor="#52c41a" />
                                    <Divider type="vertical" />
                                    {recipe.name + ' ' + recipe.id}
                                </h3>
                            ) : (
                                <h3>{recipe.name + ' ' + recipe.id}</h3>
                            )}
                            {/* </p> */}
                            {/* <h3>{recipe.name + ' ' + recipe.id}</h3> */}
                            <p>{'Version ' + recipe.version}</p>
                        </div>
                    }
                    // extra={<a href="#">More</a>}
                    style={{ width: 250 }}
                    cover={
                        <img
                            alt="example"
                            src={recipe.image.replace('http://', 'https://')}
                        />
                    }
                    className="recipe__card"
                >
                    <p>{recipe.description}</p>
                    {this.props.buttonVisible && (
                        <Button
                            onClick={() => {
                                onClose();
                                writeRecipeToDevice(recipe.id);
                            }}
                        >
                            Add
                        </Button>
                    )}

                    <Button
                        onClick={() => {
                            updateRecipeOnDevice(recipe.id);
                        }}
                    >
                        Update
                    </Button>
                </Card>
                // </Col>
            );
        });
        // console.log('fsadasd', recipes);
        return <Row gutter={16}>{recipes}</Row>;
    }
}

export default connect(mapStateToProps, actionCreators)(Recipes);
